*{
  margin: 0;
  padding:0 ;
}
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  background-color: #111;
}
li{
  display: inline;
  margin: 30px;
}
span{
  font-size: 1.7em;
  color: #fff8dc;
  text-decoration: underline;
}
span:hover{
  cursor: pointer;
}
a{
  text-decoration: none;
  color: #fff8dc;
  font-size: 1.3em;
}
a:hover{
  text-decoration: underline;
  font-size: bold;
  color: #fff7dd;
}
input{
  width:550px;
  height: 35px;
  padding: 10px;
  font-size: 1.1em;
  border: none;
  border-radius: 15px;
}
li > img:hover {
    cursor: pointer;
}
.head{
  width: 100%;
  margin: 8px 0 0 0;
}
.head > img {
  width: 100%;
  height: 600px;
}
.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  row-gap: 20px;
  background-color: #333;
  color: #fff8dc;
}

.container > .card {
  height: 300px;
  width: 300px;
  border: 2px solid #000;
  border-radius: 20px;
  background-color: #5e5e5e;
  margin: 8px 0;
  color: #fff8cc;
}
.container > .card:hover{
  box-shadow: 0 0 8px 0;
}

.container > .card > img {
  width: 80%;
  height: auto;
  margin: 5px 0 5px 30px;
  border: 1px double #000;
  border-radius: 15px;
}
.container > .card > h4{
  text-align: center;
  font-size: 1.3em;
  font-weight: bold;
  color: #212121;
}
.container > .card > .des {
  text-align: center;
  font-size: 1.04em;
  font-weight: bold;
  color: #121222;
}
.container > .card > .pri {
  text-align: center;
  font-size: 1.08em;
  color: #121222;
}
.container > .card > button {
  margin: 7px 0 7px 70px;
  width: 150px;
  height: 35px;
  font-weight: bold;
  font-size: 1.1em;
  border-radius: 8px;
}



